export default ()=> {
      return (
        <div className="question" data-index="110" dangerouslySetInnerHTML = {{ __html: `
<h6>113. 输出什么？</h6>
<pre class="language-javascript"><code class="language-javascript">console<span class="token punctuation">.</span><span class="token function">log</span><span class="token punctuation">(</span><span class="token template-string"><span class="token template-punctuation string">\`</span><span class="token interpolation"><span class="token interpolation-punctuation punctuation">\${</span><span class="token punctuation">(</span><span class="token parameter">x</span> <span class="token operator">=></span> x<span class="token punctuation">)</span><span class="token punctuation">(</span><span class="token string">'I love'</span><span class="token punctuation">)</span><span class="token interpolation-punctuation punctuation">}</span></span><span class="token string"> to program</span><span class="token template-punctuation string">\`</span></span><span class="token punctuation">)</span>
</code></pre>
<ul>
<li>A: <code>I love to program</code></li>
<li>B: <code>undefined to program</code></li>
<li>C: <code>\${(x =&gt; x)('I love') to program</code></li>
<li>D: <code>TypeError</code></li>
</ul>
<details><summary><b>答案</b></summary>
<p>
<h4>答案: A</h4>
<p>带有模板字面量的表达式首先被执行。相当于字符串会包含表达式，这个立即执行函数 <code>(x =&gt; x)('I love')</code> 返回的值. 我们向箭头函数 <code>x =&gt; x</code> 传递 <code>'I love'</code> 作为参数。<code>x</code> 等价于返回的 <code>'I love'</code>。这就是结果 <code>I love to program</code>。</p>
</p>
</details>
` }}></div>
      );
    }